import React, { Component } from 'react'
import { Accordion, Card , Button, Container} from 'react-bootstrap' ;

export class ProjectItem extends Component {
    constructor(props) {
        super(props)
    
        this.state = {
             
        }

        this.cloudOpen = this.cloudOpen.bind(this);
        this.machineOpen = this.machineOpen.bind(this);
    }
    

    cloudOpen(e){
        e.stopPropagation();
        window.location.href= "http://" + this.props.project.cloudLink;
    }

    machineOpen(e){
        e.stopPropagation();
        window.location.href= "http://" + this.props.project.machineLink;
    }

    renderMachineButton(){
        if(this.props.project.machineLink != "#0"){    
            return(
                    <Button onClick={(e)=>{ this.machineOpen(e)}} variant="dark" className="machine-project">
                                <div className="toogle-text-project">
                                        Personal
                                        <br></br>
                                        Machine
                                </div>   
                    </Button>
                )
        }else{
            return(
                <div className="machine-works-project">
                    Machine in the works
                </div>
            )
        }
    }

    renderCloudButton() {
        if(this.props.project.cloudLink != "#0"){
            return(
                <Button onClick={(e)=>{ this.cloudOpen(e) }}variant="secondary" className="cloud-project">
                    <div className="toogle-text-project">
                        Cloud
                    </div>
                </Button>
            )
        }else{
            return(
                <div className="cloud-works-project">
                    Cloud in the works
                </div>
            )
        }
    }

    render() {
        return (
            <Accordion >
                <Card>
                    <Accordion.Toggle as={Card.Header}  eventKey="0" className="project-item">
                        <Button >
                            <div className="toogle-text-project">
                                {this.props.project.name}
                            </div>
                        </Button>
                        {
                            this.renderMachineButton()
                        }

                        {
                            this.renderCloudButton()
                        }
                        
                    </Accordion.Toggle>
                    <Accordion.Collapse eventKey="0" className="project-item">
                        <Card.Body> 
                            <div className="card-text-project">
                            {this.props.project.description}
                            </div> 
                        </Card.Body>
                    </Accordion.Collapse>
                </Card>
            </Accordion>
        )
    }
}

export default ProjectItem
