import React, { Component } from 'react';
import { Button, Modal } from 'react-bootstrap' ;
import Title from './Title';
import ProjectsContainer from './ProjectsContainer';
import configs from './ProjectConfig';

import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';



export class App extends Component {
  constructor(props) {
    super(props)
  
    this.state = {
      showTechnologies : false,
      isFadeInNext : false
    }

    var timerForFade = null;
    this.technologiesClick = this.technologiesClick.bind(this);
  }

  oldPortfolioClick(){
    window.location.href="https://kanoeyesr.wixsite.com/portofolio"
  }

  technologiesClick(){

    if(this.state.showTechnologies === true){
      var thisReference = this;
      this.setState({
        isFadeInNext : false
      });

      setTimeout( 
        function(){
          thisReference.setState({
            showTechnologies : false
          });
        }
      ,1000);
    }else{
      this.setState({
        isFadeInNext : true, 
        showTechnologies: true
      });
    }
  }

  renderTechnologies(){
    if(this.state.showTechnologies){
      return(
        <Modal.Dialog className={(this.state.isFadeInNext ? "fade-in" : "fade-out") + " tech-z-order"}>
          <Modal.Header onClick={()=>this.technologiesClick()} closeButton>
            <Modal.Title>Technologies</Modal.Title>
          </Modal.Header>
  
          <Modal.Body>
            <p><b>React</b>
              <br></br>
              -react-bootstrap
              <br></br>
              <b>Personal Machine</b>
              <br></br>
              -Docker
              <br></br>
              -Nginx
            </p>
          </Modal.Body>
  
          <Modal.Footer>
            <Button onClick={()=>this.technologiesClick()} variant="secondary">Close</Button>
          </Modal.Footer>
        </Modal.Dialog>
      )
    }else{
      return(
        <div></div>
      )
    }
  }
  
  render() {
    return (
      <div>
        <Title></Title>
        <ProjectsContainer configs = {configs}></ProjectsContainer>
        
            <div className="footer-buttons-holder"> 
                    <Button onClick={()=>this.oldPortfolioClick()} className="app-old-portfolio"><div>Old Portfolio</div></Button>
                    <Button onClick={()=>this.technologiesClick()} className="app-technologies"><div>Technologies</div></Button>
            </div>
       
        {
          this.renderTechnologies()
        }
      </div>
    )
  }
}

export default App








 



