import React, { Component } from 'react';
import { ToggleButtonGroup, ToggleButton, Nav } from 'react-bootstrap' ;
import Photo from './Images/me.png'

import './Title.css'

export class Title extends Component {



    render() {
        return (
            <div>
                <Nav>
                    <Nav.Item>
                        <p className="text-justify title-text text-muted">
                        Marco Fernandes  
                        <br/>
                        Portfolio
                        </p>
                        <span className="parent-image-title">
                            <img className="image-title rounded border border-secondary"  src={Photo}></img>
                        </span>
                    </Nav.Item>
                </Nav>
                <span className="holder-title-buttons">
                    <ToggleButtonGroup className="title-buttons" type="radio" name="options" defaultValue="cloud">
                                <ToggleButton value="cloud" variant="secondary" ><div className="text-title-button">Cloud</div></ToggleButton>
                                <ToggleButton value="machine"  variant="secondary" ><div className="text-title-button">Personal <br/> Machine</div></ToggleButton>
                    </ToggleButtonGroup>
                </span>
               
            </div>
        )
    }
}

export default Title
